main {
  display: flex;
  flex: 1;
  justify-content: center; }

section {
  max-width: $layout-width;
  margin: $padding*3 0;
  width: 80%; }

article {
  time {
    display: inline-block;
    padding-bottom: $padding;
    & + span {
      @extend time;
      padding: 0 $padding/2;
      & + p {
        @extend time; } } }

  nav {
    margin: $padding*2 0;
    display: flex;
    justify-content: space-between; } }

.blog-post {
  margin-bottom: $padding;
  h2 {
    padding: 0; }
  p a, p + a {
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-left: $padding/2;
    font-family: $header-font;
    font-size: 83%;
    @include fade-from-to(rgba($text-color, 0.6), $text-color); } }

.project {
  margin-bottom: $padding;
  h1 {
    font-size: $base-font-size * 1.5; }
  h2 {
    letter-spacing: 0;
    font-size: $base-font-size;
    padding: $padding/2 0; } }


